export default {
  login: {
    username: [
      {
        required: true, message: '', trigger: 'onBlur'
      },
      {
        pattern: /^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{6,8}$/,
        message: " + 6-8",
        trigger: 'onBlur'
      }
    ],
    password: [
      {
        required: true, message: '', trigger: 'onBlur'
      },
      {
        pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d!.#*?&]{8,12}$/,
        message: "| ++ 8-12",
        trigger: 'onBlur'
      }
    ]
  },
  // /^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{6,8}$/
  // /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d!.#*?&]{8,12}$/
  //   username: "| + 6-8",
  //   password: "| ++ 8-12 "
  register: {
    username: [
      {
        required: true, message: '', trigger: 'onBlur'
      },
      {
        pattern: /^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{6,8}$/,
        message: " + 6-8",
        trigger: 'onBlur'
      }
    ],
    password: [
      {
        required: true, message: '', trigger: 'onBlur'
      },
      {
        pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d!.#*?&]{8,12}$/,
        message: "| ++ 8-12",
        trigger: 'onBlur'
      }
    ],
    email: [
      {
        required: true, message: '', trigger: 'onBlur'
      },
      {
        type: "email", message: '', trigger: 'onBlur'
      }
    ]
  },
  // username: "| + 6-8",
  //   email: '|',
  //   password: "| ++ 8-12 "
  classifyAdd: {
    classifyName: [
      {
        required: true, message: '', trigger: 'onBlur'
      }
    ]
  },
  info: {
    nickname: [
      {required: true, message: '', trigger: 'onBlur'}
    ],
    email: [
      {required: true, message: '', trigger: 'onBlur'}
    ]
  }
}